@media (max-width: 840px) {
  .dark-theme #nav ul {
    background-color: #1d1e22; }
    .dark-theme #nav ul a span {
      color: #eaeaea !important; }
  #nav {
    width: 100%; }
    #nav .nav__menu-open {
      display: block;
      position: relative;
      width: 2rem;
      height: 2rem;
      cursor: pointer;
      z-index: 102; }
      #nav .nav__menu-open.menu--opened .bar {
        background: #0752e9; }
        #nav .nav__menu-open.menu--opened .bar:nth-child(1) {
          transform: rotate(45deg);
          top: 50%;
          left: 0;
          position: absolute; }
        #nav .nav__menu-open.menu--opened .bar:nth-child(2) {
          opacity: 0; }
        #nav .nav__menu-open.menu--opened .bar:nth-child(3) {
          transform: rotate(-45deg);
          top: 50%;
          left: 0;
          position: absolute; }
      #nav .nav__menu-open .bar {
        transition: all 450ms ease-in-out;
        background: #eaeaea;
        width: 2rem;
        height: 3px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 1; }
        #nav .nav__menu-open .bar:nth-child(1) {
          transform: translate(-50%, -350%); }
        #nav .nav__menu-open .bar:nth-child(3) {
          transform: translate(-50%, 250%); }
    #nav ul {
      position: absolute;
      flex-direction: column;
      justify-content: center;
      transition: background-color 300ms ease-in-out;
      background-color: #f4f4f4;
      opacity: 1;
      display: none;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100vh;
      margin: 0;
      z-index: 100;
      padding: 0;
      transform: translateX(101%);
      transition: background-color 300ms ease-in-out, transform 650ms cubic-bezier(0.645, 0.045, 0.355, 1), color 300ms ease-in-out; }
      #nav ul a {
        padding: .75rem 1.25rem; }
        #nav ul a span {
          transition: color 300ms ease-in-out;
          font-size: 1.25rem !important;
          padding: .75rem 1.25rem;
          color: #1d1e22 !important;
          position: relative; }
          #nav ul a span::after {
            display: none; }
      #nav ul .panel-login {
        margin-left: 0;
        font-size: 1.25rem !important;
        margin-top: .5rem; }
      #nav ul.menu--opened {
