      .hero .container .container__buttons .buttons + .buttons {
        margin-top: 1rem; }
      .hero .container .container__buttons .buttons__application {
        transition: background-color 250ms ease-in-out;
        background-color: #115EF8; }
        .hero .container .container__buttons .buttons__application:hover {
          background-color: #0649d0;
